import org.w3c.dom.HTMLCanvasElement
import vision.gears.webglmath.Mat4
import vision.gears.webglmath.UniformProvider
import vision.gears.webglmath.Vec3
import vision.gears.webglmath.Vec4
import kotlin.js.Date
import kotlin.math.PI
import org.khronos.webgl.WebGLRenderingContext as GL

class Scene(
    val gl: WebGL2RenderingContext
) : UniformProvider("scene") {

    val sunPosition = Vec3(-100f, 200f, 100f)

    val vsTextured = Shader(gl, GL.VERTEX_SHADER, "textured-vs.glsl")
    val fsTextured = Shader(gl, GL.FRAGMENT_SHADER, "textured-fs.glsl")
    val vsQuad = Shader(gl, GL.VERTEX_SHADER, "quad-vs.glsl")
    val fsBackground = Shader(gl, GL.FRAGMENT_SHADER, "background-fs.glsl")
    val fsMaxBlinn = Shader(gl, GL.FRAGMENT_SHADER, "maxBlinn-fs.glsl")

    val texturedProgram = Program(gl, vsTextured, fsTextured)
    val backgroundProgram = Program(gl, vsQuad, fsBackground)
    val shadedProgram = Program(gl, vsTextured, fsMaxBlinn)

    val envTexture = TextureCube(
        gl,
        "media/posx512.jpg",
        "media/negx512.jpg",
        "media/posy512.jpg",
        "media/negy512.jpg",
        "media/posz512.jpg",
        "media/negz512.jpg"
    )

    val backgroundMaterial = Material(backgroundProgram).apply {
        this["envTexture"]?.set(envTexture)
    }

    val texturedQuadGeometry = TexturedQuadGeometry(gl)
    val backgroundMesh = Mesh(backgroundMaterial, texturedQuadGeometry)

    val lights = Array<Light>(8) { Light(it, *Program.all) }

    var score = 0

    val jsonLoader = JsonLoader()

    val slowpokeMeshes = jsonLoader.loadMeshes(gl,
        "media/slowpoke/slowpoke.json",
        Material(texturedProgram).apply {
            this["colorTexture"]?.set(
                Texture2D(gl, "media/slowpoke/YadonDh.png")
            )
            this["envTexture"]?.set(envTexture)
        },
        Material(texturedProgram).apply {
            this["colorTexture"]?.set(
                Texture2D(gl, "media/slowpoke/YadonEyeDh.png")
            )
            this["envTexture"]?.set(envTexture)
        }
    )

    val chevyMesh = jsonLoader.loadMeshes(
        gl,
        "media/chevy/chassis.json",
        Material(texturedProgram).apply {
            this["colorTexture"]?.set(
                Texture2D(gl, "media/chevy/chevy.png")
            )
            this["envTexture"]?.set(envTexture)
            this["lightPos"]?.set(sunPosition)
        },
    )

    val wheelMesh = jsonLoader.loadMeshes(
        gl,
        "media/chevy/wheel.json",
        Material(texturedProgram).apply {
            this["colorTexture"]?.set(
                Texture2D(gl, "media/chevy/chevy.png")
            )
            this["envTexture"]?.set(envTexture)
            this["lightPos"]?.set(sunPosition)
        },
    )

    val soccerMesh = jsonLoader.loadMeshes(gl, "media/sphere/sphere.json",
        Material(texturedProgram).apply {
            this["colorTexture"]?.set(
                Texture2D(gl, "media/sphere/ball.png")
            )
            this["envTexture"]?.set(envTexture)
            this["lightPos"]?.set(sunPosition)
        }
    )

    val groundMaterial = Material(texturedProgram).apply {
        this["colorTexture"]?.set(
            Texture2D(gl, "media/grass.jpg")
        )
        this["envTexture"]?.set(envTexture)
    }
    val groundMesh = Mesh(groundMaterial, GroundGeometry(gl))
    val boxMesh = Mesh(groundMaterial, TriangleGeometry(gl))

    val avatar = GameObject(*this.chevyMesh).apply {
        position.set(Vec3(85f, 7f, 0f))
        descriptor = "avatar"
        yaw += (3*PI / 2f).toFloat()
    }
    val ball = GameObject(*soccerMesh).apply {
        scale *= 25f
        position.y += 25f
        descriptor = "ball"
    }

    val gameObjects = ArrayList<GameObject>()

    init {
        gameObjects += avatar
        gameObjects += ball

        gameObjects += GameObject(*slowpokeMeshes).apply {
            scale *= 30f
            position.x -= 500f
            position.y -= 170f
            yaw = PI.toFloat() / 2f
            pitch = -PI.toFloat() / 4f
            collisionRadius = 230f
            descriptor = "goal"
        }

        gameObjects += GameObject(*slowpokeMeshes).apply {
            scale *= 30f
            position.x += 500f
            position.y -= 170f
            yaw = -PI.toFloat() / 2f
            pitch = -PI.toFloat() / 4f
            collisionRadius = 230f
            descriptor = "goal"
        }

        gameObjects += GameObject(groundMesh).apply {
            pitch = PI.toFloat() / 2f
            scale *= 100f //(Vec3(200f))
            descriptor = "ground wheel" //adding wheel so it is ignored
            position.y = -0.1f
        }

//        Rear 2 wheels
//        left
        gameObjects += GameObject(* wheelMesh).apply {
            parent = avatar
//            yaw = PI.toFloat()
            position.set(Vec3(6.5f, -4f, -11.1f))
            descriptor = "wheel"
        }
//        right
        gameObjects += GameObject(*wheelMesh).apply {
            parent = avatar
            position.set(Vec3(-6.5f, -4f, -11.1f))
            descriptor = "wheel"
        }

//        Front 2 wheels
//        left
        gameObjects += GameObject(*wheelMesh).apply {
            parent = avatar
//            v is ride height
            position.set(Vec3(6.5f, -4f, 13.85f))
//            yaw = PI.toFloat()
            descriptor = "front left wheel"
        }
//        right
        gameObjects += GameObject(*wheelMesh).apply {
            parent = avatar
            position.set(Vec3(-6.5f, -4f, 13.85f))
            descriptor = "front wheel"
        }

        lights[0].position.set(1.0f, 1.0f, 1.0f, 0.0f).normalize()
        lights[0].powerDensity.set(1.0f, 1.0f, 0.0f)
        lights[1].position.set(-1.0f, -1.0f, 1.0f, 0.0f).normalize()
        lights[1].powerDensity.set(0.0f, 1.0f, 1.0f)
    }

    val shadowMatrix by Mat4()
    val shadow by Vec4()
//    val lightPos by Vec3()

    val camera = PerspectiveCamera(*Program.all).apply {
        position.set(0f, 65f, 200f)
    }

    fun resize(canvas: HTMLCanvasElement) {
        gl.viewport(
            0,
            0,
            canvas.width,
            canvas.height
        )//#viewport# tell the rasterizer which part of the canvas to draw to ˙HUN˙ a raszterizáló ide rajzoljon
        camera.setAspectRatio(canvas.width.toFloat() / canvas.height)
    }

    val timeAtFirstFrame = Date().getTime()
    var timeAtLastFrame = timeAtFirstFrame

    init {
        gl.enable(GL.DEPTH_TEST)
        addComponentsAndGatherUniforms(*Program.all)
    }

    fun resetGameObjects() {
        avatar.position.set(Vec3(85f, 7f, 0f))
        avatar.yaw = -(PI / 2f).toFloat()
        avatar.velocity.timesAssign(0f)
        ball.position.set(Vec3(0f, 25f, 0f))
        ball.velocity.timesAssign(0f)
    }

    @Suppress("UNUSED_PARAMETER")
    fun update(keysPressed: Set<String>) {
        val timeAtThisFrame = Date().getTime()
        val dt = (timeAtThisFrame - timeAtLastFrame).toFloat() / 1000.0f
        val t = (timeAtThisFrame - timeAtFirstFrame).toFloat() / 1000.0f
        timeAtLastFrame = timeAtThisFrame

        camera.move(t, dt, keysPressed, avatar, ball)

        var turning = 0f
        var force = 0f
        var torque = 0f

        if ("UP" in keysPressed) {
            force = 150f
            camera.moveMode = "1p"
        }
        if ("DOWN" in keysPressed) {
            force = -100f
            camera.moveMode = "1p"
        }
        if ("LEFT" in keysPressed) {
            torque = 2f
            turning = 0.5f
        }
        if ("RIGHT" in keysPressed) {
            torque = -2f
            turning = -0.5f
        }
        if ("N" in keysPressed) {
            force *= 5f
        }
        if ("R" in keysPressed) {
            resetGameObjects()
        }

        gameObjects.forEach {
            if (it.parent == avatar) {
                if (it.descriptor.contains("front")) {
                    it.yaw = turning
                }
            }
            if (it == avatar) {
                it.force = force
                it.torque = torque
            }
//            it["lightPos"]?.set(sunPosition.plus(it.position))
        }

        gl.clearColor(0.3f, 0.0f, 0.3f, 1.0f)//## red, green, blue, alpha in [0, 1]
        gl.clearDepth(1.0f)//## will be useful in 3D ˙HUN˙ 3D-ben lesz hasznos
        gl.clear(GL.COLOR_BUFFER_BIT or GL.DEPTH_BUFFER_BIT)//#or# bitwise OR of flags

        gl.enable(GL.BLEND)
        gl.blendFunc(
            GL.SRC_ALPHA,
            GL.ONE_MINUS_SRC_ALPHA
        )

        backgroundMesh.draw(camera)

        gameObjects.forEach {
            it.move(dt, t, keysPressed, gameObjects)
        }
        gameObjects.forEach { it.update() }

        shadowMatrix.set()
        shadow.set(Vec4.ones)
        gameObjects.forEach { it.draw(this, camera) }

//        hardcoded "sun"
        val lx = sunPosition.x
        val ly = sunPosition.y
        val lz = sunPosition.z
        shadowMatrix.set(
            Mat4(
                1f,
                0.0f,
                0.0f,
                0.0f,
                (-lx / ly),
                0f,
                (-lz / ly),
                0.0f, // -lx/ly, 0, -lz/ly, 0 where lx, ly, and lz all are a vector from the origin pointing to the light source
                0.0f,
                0.0f,
                1f,
                0.0f,
                0.0f,
                0.001f,
                0f,
                1.0f
            )
        )

//        lightPos.set(sunPosition.plus(avatar.position))

//        og way, directly below
//        shadowMatrix.set().scale(1f, 0f, 1f).translate(0f, 0.00001f, 0f)
        shadow.set(0f, 0f, 0f, 1f)
//        gameObjects.forEach { it.draw(this, camera) }
        gameObjects.forEach {
            if (!it.descriptor.contains("ground") && it.descriptor != "goal") {
                it.draw(this, camera)
            }
        }

        if (score < ball.score) {
            score = ball.score
            resetGameObjects()
        }
    }
}
