import vision.gears.webglmath.Mat4
import vision.gears.webglmath.UniformProvider
import vision.gears.webglmath.Vec3
import vision.gears.webglmath.times
import kotlin.math.cos
import kotlin.math.exp
import kotlin.math.pow
import kotlin.math.sin

open class GameObject(
    vararg val meshes: Mesh
) : UniformProvider("gameObject") {

    var descriptor = ""
    var modelMatrixInverse = Mat4()
    val position = Vec3()
    var velocity = Vec3()
    var acceleration = Vec3()
    var angularVelocity = 0f
    var dampedAngularVel = 0f
    var angularAcceleration = 0f
    var ahead = Vec3()
    var aheadVelocity = Vec3()
    var sideVelocity = Vec3()

    var mass = 1.0f
    var force = 0.0f
    var torque = 0.0f

    var roll = 0.0f
    var yaw = 0.0f
    var pitch = 0.0f

    val scale = Vec3(1.0f, 1.0f, 1.0f)
    var turnFactor = 0f
    var collisionRadius = 40f
    val modelMatrix by Mat4()

    val rotationMatrix by Mat4()

    var score = 0

    var parent: GameObject? = null

    init {
        addComponentsAndGatherUniforms(*meshes)
    }

    fun update() {
        modelMatrix.set()
            .scale(scale)
            .rotate(roll)
            .rotate(pitch, 1.0f, 0.0f, 0.0f)
            .rotate(yaw, 0.0f, 1.0f, 0.0f)
        modelMatrix *= rotationMatrix
        modelMatrix.translate(position)
        parent?.let { parent ->
            modelMatrix *= parent.modelMatrix
        }
//        val temp = modelMatrix
//        modelMatrixInverse.set(temp.invert())
    }

    open inner class Motion {
        open operator fun invoke(
            dt: Float = 0.016666f,
            t: Float = 0.0f,
            keysPressed: Set<String> = emptySet<String>(),
            gameObjects: List<GameObject> = emptyList<GameObject>()
        ): Boolean {

//            collisionMotion(gameObjects)

            gameObjects.forEach {
//                 && (this@GameObject.descriptor == "chevy" || this@GameObject.descriptor == "ball")
                if (it != this@GameObject && !this@GameObject.descriptor.contains("wheel")) {
                    if ((it.position - position).length() < collisionRadius && !it.descriptor.contains("wheel")) {
                        if (it.descriptor.equals("goal") && this@GameObject.descriptor.equals("ball")){
                            this@GameObject.score += 1
                            console.log("we got the contact")
                        } else if (it.descriptor.equals("ball") && this@GameObject.descriptor.equals("goal")){
                            it.score += 1
                        } else {
                            var diffNorm = (it.position - position).normalize()
                            it.position += diffNorm * .01f
                            position -= diffNorm * .01f

                            var relVel = it.velocity - velocity
                            var impulse = diffNorm.dot(relVel) / (1f / 1f + 1f / 1f) * (1f + 0.5f)
                            it.velocity = it.velocity.plus(-impulse / 1f * diffNorm)
                            velocity = velocity.plus(impulse / 1f * diffNorm)
//                            if (it.descriptor.equals("ball") || this@GameObject.descriptor.equals("ball")) {
//                                it.velocity = it.velocity.times(2f)
//                            }
                        }
                    }
                }
            }

//                                                        gravity ... ? lol
            acceleration.set(Vec3(0f, -75.0f, 0f))

            // don't move the ground or goals
            if (descriptor.contains("ball")) {
                velocity = velocity.plus(acceleration * dt)

                position += velocity * dt
                if (position.y < 25f) {
                    position.y = 25f
                    velocity.y = 0f
                }

//              ball rolling
                yaw = 0f
                pitch = 0f
                roll = 0f
                rotationMatrix.rotate(-velocity.length() / 25f * dt, velocity.cross(Vec3(0f, 1f, 0f)))

                velocity = velocity.times(0.99f)
            }

            if (descriptor.equals("avatar")) {
                carMotion(dt)
//                velocity = velocity.plus(acceleration * dt)
                position += velocity * dt
                position.y = 7f
            } else if (descriptor.contains("wheel")) {
                carMotion(dt)
            }

            return true
        }

        open fun carMotion(dt: Float) {
            // --- ROTATIONAL --- //
//            Sigmoid function https://en.wikipedia.org/wiki/Sigmoid_function
            turnFactor = 1f / (1f + exp(-0.3f * velocity.length() + 6f))

//            flip turn angle when going in reverse
            if (force < 0) {
                torque *= -1
            }
            angularAcceleration = torque * mass.pow(-1)
            angularVelocity += angularAcceleration * dt
            dampedAngularVel =
                torque / (1f + exp(-8f * angularVelocity + 6f)) //another sigmoid function https://www.desmos.com/calculator/kn9tpwdan5
            yaw = yaw.plus(dampedAngularVel * dt * turnFactor)


            // --- DIRECTIONAL --- //
            ahead = Vec3(sin(yaw), 0f, cos(yaw)).normalize()
            acceleration.set(Vec3(force * sin(yaw), 0f, force * cos(yaw)) * mass.pow(-1))
            velocity.plusAssign(acceleration * dt)

            aheadVelocity = ahead.times(ahead.dot(velocity))
            sideVelocity = velocity.minus(aheadVelocity)
            velocity.timesAssign(Vec3.zeros) // note you cannot just say velocity = Vec3.zeros, this does not work

            velocity.plusAssign(aheadVelocity * 0.5f.pow(dt)) //TODO: relatively untuned constant
            velocity.plusAssign(sideVelocity * 0.00005f.pow(dt)) // attenuate out the "drifting", smaller constant

            // --- WHEEL ROLLING --- //
            pitch = pitch.minus(
                (parent?.velocity?.dot(
                    Vec3(-sin(parent?.yaw ?: 0f).toFloat(), 0f, -cos(parent?.yaw ?: 0f).toFloat())
                )
                    ?: 0f) * 0.008f // TODO: may need to tune this in a bit more, it dictates how fast the wheels move in relation to the speed of the car
            )
        }

        open fun collisionMotion(gameObjects: List<GameObject>) {
            gameObjects.forEach {
                if (it != this@GameObject) {
                    if ((it.position - position).length() < 0.6f) {
                        var diffNorm = (it.position - position).normalize()
                        it.position += diffNorm * .01f
                        position -= diffNorm * .01f
                        var relVel = it.velocity - velocity
                        var impulse = diffNorm.dot(relVel) / (1f / 1f + 1f / 1f) * (1f + 0.5f)
                        it.velocity = it.velocity.plus(-impulse / 1f * diffNorm)
                        velocity = velocity.plus(impulse / 1f * diffNorm)

                    }
                }
            }
//            rotationMatrix.rotate(-velocity.length()/0.3f*dt, velocity.cross(Vec3(0f,1f,0f)))

        }

    }

    var move = Motion()

    fun control(dt: Float, keysPressed: Set<String>) {
        acceleration.set()

        if ("UP" in keysPressed) {
            acceleration.plusAssign(Vec3(this.mass * 2))
        }
        if ("DOWN" in keysPressed) {
            acceleration.plusAssign(Vec3(this.mass * -2))
        }
        if ("LEFT" in keysPressed) {
        }
        if ("RIGHT" in keysPressed) {
        }
    }

}
