import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.dom.addClass
import kotlinx.dom.removeClass
import org.w3c.dom.HTMLCanvasElement
import org.w3c.dom.HTMLDivElement
import org.w3c.dom.events.Event
import org.w3c.dom.events.KeyboardEvent
import org.w3c.dom.events.MouseEvent

class App(val canvas: HTMLCanvasElement, val overlay: HTMLDivElement) {

    val keysPressed = HashSet<String>()
    private var gameStarted = false

    val gl = (canvas.getContext("webgl2", object {
        val alpha = false
    })
        ?: throw Error("Browser does not support WebGL2")) as WebGL2RenderingContext //#alpha# never make canvas transparent ˙HUN˙ ne legyen áttetsző a vászon

    val scene =
        Scene(gl)//#scene# this object is responsible for resource allocation and drawing ˙HUN˙ ez az objektum felel az erőforrások kezeléséért és kirajzolásáért

    init {
        resize()//## we adjust render resolution in a separate method, as we will also need it when the canvas is resized ˙HUN˙ rajzolási felbontás beállítása külön metódusban, mert ablakátméretezéskor is kell majd ugyanez
    }

    fun resize() {
        canvas.width =
            canvas.clientWidth//#canvas.width# rendering resolution ˙HUN˙ rajzolási felbontás #canvas.clientWidth# canvas size ˙HUN˙ a vászon mérete
        canvas.height = canvas.clientHeight
        scene.resize(canvas)
    }

    @Suppress("UNUSED_PARAMETER")
    fun registerEventHandlers() {
        document.onkeydown = { //#{# locally defined function
                event: KeyboardEvent ->
            keysPressed.add(keyNames[event.keyCode])
        }

        document.onkeyup = { event: KeyboardEvent ->
            keysPressed.remove(keyNames[event.keyCode])
        }

        canvas.onmousedown = { event: MouseEvent ->
            // LABTODO: pass to camera
            scene.camera.mouseDown()
            event
        }

        canvas.onmousemove = { event: Event ->
            // LABTODO: pass to camera
            scene.camera.mouseMove(event as MouseEvent)
            event.stopPropagation()
        }

        canvas.onmouseup = { event: Event ->
            // LABTODO: pass to camera
            scene.camera.mouseUp()
            event // This line is a placeholder for event handling code. It has no effect, but avoids the "unused parameter" warning.
        }

        canvas.onmouseout = { event: Event ->
            event // This line is a placeholder for event handling code. It has no effect, but avoids the "unused parameter" warning.
        }

        window.onresize = { event: Event ->
            resize()
        }

        window.requestAnimationFrame {//#requestAnimationFrame# trigger rendering
            update()//#update# this method is responsible; for drawing a frame
        }
    }

    fun update() {
        scene.update(keysPressed)
        if (("SPACE" in keysPressed || "UP" in keysPressed) && !gameStarted) {
            gameStarted = true
            canvas.removeClass("darkClass")
        }

        if (gameStarted) {
            overlay.style.top = "10px"
            overlay.innerHTML =
                """<h2>Score: """ + scene.score + """</h2>"""
        }

        window.requestAnimationFrame { update() }
    }
}

fun main() {
//    val container = document.getElementById("container") as HTMLCanvasElement
    val canvas = document.getElementById("canvas") as HTMLCanvasElement
    val overlay = document.getElementById("overlay") as HTMLDivElement
    overlay.innerHTML =
        """<h1>LocketReague</h1><p>Welcome to LocketReague!</p><p>Control the car with the arrowkeys, and try to get the ball into the goal.</p><p>Press the spacebar to start!</p>"""
    canvas.addClass("darkClass")
    overlay.style.top = "80px"

    try {
        val app = App(canvas, overlay)//#app# from this point on,; this object is responsible; for handling everything
        app.registerEventHandlers()//#registerEventHandlers# we implement this; to make sure the app; knows when there is; something to do
    } catch (e: Error) {
        console.error(e.message)
    }
}